import React, { useState } from 'react';
import Message from './comments/compontents/message/submessage/Message';




export default function MessageScroll(props) {

    const { post } = props;
    const [message, setMessage] = useState([]);
    const [showBottonBar, setShowBottonBar] = useState(true)

    



    return (
        <>
            {post.comments.map((comment, index) => {
                return <div>{!post.comments.is_replies && <Message editable={false} comment={comment} />}</div>;
            })}


            <div className="buttonBar">
            {/* {post.comments.length < 2 && showBottonBar ? <div className="loader"></div>: null}     */}
            </div>
        </>
    );
}

